.slider-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.slider-container .swiper {
  width: 100%;
  height: 40vw;
}

.slider-container .swiper-slide {
  text-align: center;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider-container .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-slide-overlay {
  position: relative;
  overflow: hidden;
}

.swiper-slide-overlay img {
  width: 100%;
  height: auto;
  display: block;
}

.swiper-slide-overlay::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0));
  pointer-events: none;
}

.slide-content {
  position: absolute;
  left: 0;
  bottom: 10vw;
  background: linear-gradient(to right, rgba(10, 44, 60, 0.9), rgba(10, 44, 60, 0.8), rgba(10, 44, 60, 0.0));
  padding: 2vw 5vw;
  padding-bottom: 3vw;
  width: 800px;
  z-index: 999;
}

.slide-text {
  font: 40px "Roboto Slab", serif;
  color: #fff5e1;
}

.slide-span{
  color: #f89715;
}

.slide-content .slide-btn{
  padding: 0.8vw 3vw;
  background-color: #f89715;
  font: bold 20px "Roboto Slab", serif;
  transition: all 0.3s ease;
  cursor: pointer;
  border: none;
  outline: none;
  border-radius: 4px;
  color: #fff5e1;
  box-shadow: 0 6px 12px rgba(248, 151, 21, 0.5);
}

.slide-btn:hover{
  background-color: #e08510;
  box-shadow: 0 6px 12px rgba(224, 133, 16, 0.7);
}
